@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* Namn signaturen */
.sign {
  font-family: 'Homemade Apple', cursive;
  text-align: center;
  font-size:3EM;
}

.App-link {
  color: purple;
  text-align: center;
}

.presentation-title{
  text-align: center;
}
.about-title{
  text-align: center;
}
.work-title{
  text-align: center;
}
.education-title{
  text-align: center;
}
.project-title{
  text-align: center;
}
.contact-title{
  text-align: center;
}
.description{
text-align: center;
}
.site-footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  padding: 5px 0;
  background-color: black;
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
